import { WorkState } from "../game/config/DataStruct"

class WorkCatModel {
    catsInfo: any[] = []; 
    currentProgress: number = 0
    currentWorkId: string = '0'
    currentStory: string = ''
    currentWorkResultImg = ''
    finalEarn = 0
    shareDone: boolean = true
    state : WorkState = WorkState.WORKING

    resetWorkInfo(){
        this.currentProgress = 0
        this.currentWorkId = '0'
        this.currentStory = ''
        this.currentWorkResultImg = ''
    }
}
export default new WorkCatModel()